import Logger from "../../clients/logger";
import SystemLog from "./systemLog";
import { BootEngine } from "./bootEngine";
import { killOldApplications } from "./common"
import { ServiceLauncher } from "./serviceLauncher";
import { ShutdownManager } from "./shutDownManager";
import { System } from "../../common/system";
import { ConfigUtilInstance as  ConfigUtil } from "../../common/configUtil";
import { InitializeDeepLinkingTask } from "./bootTasks/initializeDeepLinkingTask";
import { InitializeSystemStateHandersTask } from "./bootTasks/initializeSystemStateHandersTask";
import { InitializeRouterTask } from "./bootTasks/initializeRouterTask";
import { InitializeFinsemblePubsubTask } from "./bootTasks/initializeFinsemblePubsubTask";
import { WaitForAuthenticatedTask } from "./bootTasks/waitForAuthenticatedTask";
import { InitializeSystemManagerAPITask } from "./bootTasks/initializeSystemManagerAPITask";
import { StartLoggerTask } from "./bootTasks/startLoggerTask";
import { RegisterHotkeysTask } from "./bootTasks/registerHotKeysTask";
import { SetupSearchLauncherTask } from "./bootTasks/setupSearchLauncherTask";
import { LoadUserDefinedComponentsTask } from "./bootTasks/loadUserDefinedComponentsTask";
import { LoadSystemTrayIconTask } from "./bootTasks/loadSystemTrayIconTask";
import { ForceErrorTestTask } from "./bootTasks/testTasks/forceErrorTestTask";
import { CheckpointErrorTestTask } from "./bootTasks/testTasks/checkpointErrorTestTask";
import { CheckpointBadDependencyTestTask } from "./bootTasks/testTasks/checkpointBadDependencyTestTask";
import { CheckpointCircularDependencyTestTask } from "./bootTasks/testTasks/checkpointCircularDependencyTestTask";
import { BootTaskCallbackInterface } from "./_types";

declare global {
	interface Window {
		systemManager: SystemManager;
		bootEngine: BootEngine;
		helpData: Function;
	}
}

// catch and errors here that were missed and log them
window.addEventListener("error", function (errorObject) {
	// // take the event error object and strip off irrelevant parts of the call stack, format the message, then output to the system log since error here is likely catastrophic
	var stack = errorObject.error && errorObject.error.stack ? errorObject.error.stack.substring(errorObject.error.stack.search("at ")) : "";
	var systemLogErr = `System Manager: ${errorObject.message}<br />File: ${errorObject.filename}<br />Line: ${errorObject.lineno}<br />Column: ${errorObject.colno}<br />${stack}`;
	SystemLog.error({ leadingBlankLine: true }, systemLogErr);
	return false;
});

/**
 * The system manger boots Finsemble and hosts system-wide resources including the system log.
 * The system manager's URL is identified in the manifest -- it always starts first, then the
 * bootEngine starts the other services and components.
 */
export class SystemManager {
	onErrorMakeSystemManagerVisible: boolean;
	bootEngine: BootEngine;
	finUUID: object; // this applications UUID
	manifest: any;
	serviceLauncher: ServiceLauncher;
	shutdownManager: ShutdownManager;
	routerClient;
	logger;

	constructor(params) {
		console.debug("Starting System Manager", params);
		this.manifest = params.manifest;
		this.initialize();
	}

	/**
	 * Initializes the system manager
	 */
	private async initialize() {
		// initialize system log first since it may be needed for error output
		this.onErrorMakeSystemManagerVisible = ConfigUtil.getDefault(this.manifest, "manifest.finsemble.bootConfig.onErrorMakeSystemManagerVisible", false);
		SystemLog.initialize(this.onErrorMakeSystemManagerVisible);

		//make sure the essential config is available
		this.confirmConfigAndReport(this.manifest);

		// get the finsemble UUID with is unique system-wide and the same across restarts
		this.finUUID = System.Application.getCurrent().uuid;
		console.log("finUUID", this.finUUID);

		// if old finsemble "applications" left around from when Finsemble was last running, kill then now before a fresh boot starts everything
		if (!(this.manifest.finsemble.killOrphanApplications === false)) {
			console.log("killing applications")
			await killOldApplications(this.finUUID);
		}
	}

	/**
	 * Display error messages if required finsemble config is not available. If any of these config values are missing then Finsemble will not start correctly.
	 * @param manifest the initial boot manifest
	 */
	private confirmConfigAndReport(manifest) {
		let userNotification = require("../../common/userNotification").default;

		// definitions to drive verification of config object
		const REQUIRED_STRING = ConfigUtil.REQUIRED_STRING;
		const REQUIRED_OBJECT = ConfigUtil.REQUIRED_OBJECT;
		var configVerifyObject = {
			finsemble: {
				applicationRoot: REQUIRED_STRING,
				moduleRoot: REQUIRED_STRING,
				bootTasks: REQUIRED_OBJECT,
				system: {
					FSBLVersion: REQUIRED_STRING,
					requiredServicesConfig: REQUIRED_OBJECT
				},
				workspaceTemplates: REQUIRED_OBJECT,
				servicesRoot: REQUIRED_STRING
			}
		};

		// do an initial sanity check on the boot config
		var configOk = ConfigUtil.verifyConfigObject("manifest", manifest, configVerifyObject);

		// if any config problems at this level then catastrophic, so report error "everywhere"; logger may not come up so use console too
		if (!configOk) {
			let errorMsg = "Manifest Error: probably cause is the `finsemble` JSON in manifest file is not correct. See system manager console errors for more information.";
			SystemLog.error({}, errorMsg);
			console.error(errorMsg, "manifest.finsemble", manifest.finsemble);
			Logger.system.error("forceObjectsToLogger", errorMsg, "manifest.finsemble", manifest.finsemble);
			// notification URL is not pulled from finsemble.notificationURL because config may be corrupted
			userNotification.alert("system", "ONCE-SINCE-STARTUP", "FSBL-Internal-MANIFEST-Error", errorMsg);
		} else {
			Logger.system.log("APPLICATION LIFECYCLE:STARTUP: Finsemble Version", manifest.finsemble.system.FSBLVersion);
		}
	}

	/**
	 * help function to display on console the key diagnostic data
	 */
	public helpData() {
		console.log("1) finsemble.bootConfig onErrorMakeSystemManagerVisible:", this.onErrorMakeSystemManagerVisible);
		console.log("");
		this.bootEngine.helpData();
		return ("helpData complete"); // for cleaner console output
	}

	/**
	 * Creates Boot Engine and runs it to bring on Finsemble.
	 */
	public async boot() { // left public for testing and debugging
		try {
			this.shutdownManager = new ShutdownManager({ finUUID: this.finUUID, manifest: this.manifest });
			this.serviceLauncher = new ServiceLauncher( { finUUID: this.finUUID, manifest: this.manifest, shutdownManager: this.shutdownManager } );

			window.bootEngine = this.bootEngine = new BootEngine(this.manifest, this.serviceLauncher, this.shutdownManager);

			this.registerBootTasks(this.bootEngine, this.manifest, this.serviceLauncher);

			this.bootEngine.run();
		} catch (err) {
			SystemLog.error({ leadingBlankLine: true }, err);
		}
	}

	/**
	 * Registers all the boot tasks before running bootEngine
	 * @param bootEngine
	 * @param manifest
	 * @param serviceLauncher
	 */
	private registerBootTasks(bootEngine: BootEngine, manifest, serviceLauncher: ServiceLauncher) {
		let regAF: Function; // var to assign arrow functions to so won't be anonymous in stack trace

		this.bootEngine.registerBootTask("initializeDeepLinkingTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let initializeDeepLinkingTask = new InitializeDeepLinkingTask();
			initializeDeepLinkingTask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("initializeRouterTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let initializeRouterTask = new InitializeRouterTask({ manifest, serviceLauncher });
			initializeRouterTask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("initializeFinsemblePubsubTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let initializeFinsemblePubsubTask = new InitializeFinsemblePubsubTask();
			initializeFinsemblePubsubTask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("initializeSystemStateHandersTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let initializeSystemStateHandersTask = new InitializeSystemStateHandersTask();
			initializeSystemStateHandersTask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("testTask_initializeSplinterAgentPool", regAF=(doneCallback: BootTaskCallbackInterface) => {
			this.bootEngine.initializeSplinterAgentPoolTask(doneCallback);
		});

		this.bootEngine.registerBootTask("waitForAuthenticatedTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let waitForAuthenticatedTask = new WaitForAuthenticatedTask();
			waitForAuthenticatedTask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("initializeSystemManagerAPITask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let initializeSystemManagerAPITask = new InitializeSystemManagerAPITask({ bootEngine, systemLog:SystemLog });
			initializeSystemManagerAPITask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("startLoggerTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let startLoggerTask = new StartLoggerTask();
			startLoggerTask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("loadUserDefinedComponentsTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let loadUserDefinedComponentsTask = new LoadUserDefinedComponentsTask();
			loadUserDefinedComponentsTask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("registerHotkeysTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let registerHotkeysTask = new RegisterHotkeysTask();
			registerHotkeysTask.start(doneCallback);
		});;

		this.bootEngine.registerBootTask("setupSearchLauncherTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let setupSearchLauncherTask = new SetupSearchLauncherTask();
			setupSearchLauncherTask.start(doneCallback);
		});

		this.bootEngine.registerBootTask("loadSystemTrayIconTask", regAF=(doneCallback: BootTaskCallbackInterface) => {
			let loadSystemTrayIconTask = new LoadSystemTrayIconTask();
			loadSystemTrayIconTask.start(doneCallback);
		});

		// test tasks -- load them so can test with only a config change. By default they are disabled (i.e. their bootParams.autostart is false)
		// the start function for these test tasks can be passed in directly since there's no binding problem (the test tasks won't reference `this`)
		this.bootEngine.registerBootTask("forceErrorTestTask", new ForceErrorTestTask().start);
		this.bootEngine.registerBootTask("checkpointErrorTestTask", new CheckpointErrorTestTask().start);
		this.bootEngine.registerBootTask("checkpointCircularDependencyTestTask", new CheckpointBadDependencyTestTask().start);
		this.bootEngine.registerBootTask("checkpointBadDependencyTestTask", new CheckpointCircularDependencyTestTask().start);
	}

}

// first code to run after the underlying container (e.g. OpenFin, Electron) is ready. Display and erors in console too in case logger doesn't come up
System.ready(function () {
	ConfigUtil.getInitialManifest(async function (manifest) { // get the "bootstrap" manifest, which is the minimum
		// the follow logger and console output is referenced in FAQ for diagnostics, so don't change
		console.log("APPLICATION LIFECYCLE:STARTUP: *** Initial manifest ***", manifest);
		Logger.system.log("forceObjectsToLogger", "APPLICATION LIFECYCLE:STARTUP: *** Initial manifest ***", manifest);

		let systemManager = new SystemManager({ manifest });

		// start Finsemble
		systemManager.boot();

		// window vars for debugging + allows helpData() to be ran directly from chromium console (at window level)
		window.systemManager = systemManager;
		window.helpData = window.systemManager.helpData.bind(window.systemManager);
	});
});





